@import "../../../scss/helpers/placeholder";
@import "../../../scss/helpers/mixins";

$prefix: '.rise-blocks';
$caption-width: 650px;
$banner-caption-width: 1140px;
$text-content-padding: 16px;

%banner-overlay {
	content: '';
	position: absolute;
	top: 0;
	width: 100%;
	height: 100%;
	left: 0;  
	background: rgba(245, 245, 245, 0.6);
}

#{$prefix}-banner-has-bg{
	position: relative;
	background-size: cover !important;
	background-repeat: no-repeat !important;
	height: 65vh;
	padding: 0 15px;

	#{$prefix}-banner-overlay {
		@extend %banner-overlay;
	}

	a {
		&:focus{
			outline: none;
			box-shadow: none;
		}
	}

	/* banner content */
	#{$prefix}-banner-caption {    
	    position: relative;
	    max-width: $banner-caption-width;
	    margin: auto;    
	    height: 100%;

	    #{$prefix}-banner-title {
		    margin-bottom: 16px;
	      	text-transform: capitalize;
	      	margin-top: 0;	      	
	    }

	    #{$prefix}-banner-content {
	        max-width: $caption-width;
	        position: absolute;
	        top: 50%;
	        transform: translateY(-50%);
	    }

	    #{$prefix}-banner-text-content {
	        padding-top: $text-content-padding;
	        padding-bottom: $text-content-padding;	        
	    }
	}

	/* banner button */
	#{$prefix}-banner-btn-group {
	    margin-top: 40px;

	    #{$prefix}-banner-btn {
		    vertical-align: middle;
		    padding: 16px 24px;
		    text-transform: uppercase;
		    transition: all .3s;
		    text-decoration: none;
		    &:focus {
		    	outline: none;
		    }
		}
	}
}

/* slick dots */
#{$prefix}-banner-slider-init {

    .slick-dots{
        position: absolute;
        bottom: 25px;
        margin: auto;
        padding: 0;
        left: 50%;
        transform: translate(-50%);
        z-index: 99;

        > li{
            &:only-child {
                display: none;   
            }   
        }
        
        li{
            position: relative;
            display: inline-block;
            padding: 0;
            cursor: pointer;
            margin: 2px 3px;   

            button{
                font-size: 0;
                line-height: 0;
                width: 30px;
                height: 6px;
                padding: 1px;            
                color: #fff;
                border: 1px solid;
                outline: 0;
                background: transparent;
            }            
        }
    }
}

/* slick arrow */

%arrow-style {
    position: absolute;
    z-index: 9;
    color: #000;
    padding: 8px;
    line-height: 0;
    font-size: 20px;
    width: 41px;
    text-align: center;
    transition: all .3s;
    background: transparent;
    border: 2px solid;   
    border-color: #fff; 
    background: #fff;
    cursor: pointer;
    
    &:focus {
    	outline: none;
    }

}

#{$prefix}-banner-slider-init {

    #{$prefix}-prev-arrow {

        &#{$prefix}-slider-arrow {
            left: 18px;
            top: 50%;
            transform: translate(0, -50%);        
        }

        &.arrow-right {
            left: 18px;
            top: 0;
        }
        
    	@extend %arrow-style;
    }

    #{$prefix}-next-arrow {

        &#{$prefix}-slider-arrow{
            right: 18px;
            top: 50%;
            transform: translate(0, -50%);        
        }
        
        &.arrow-right {
            right: 15px;
            top: 0;
        }
        
    	@extend %arrow-style;
    }

}

.fade-zoom {
   .rise-blocks-banner-has-bg {
      transition: all 1s ease-in-out;
      transform: scale(1.25, 1.25);
   } 

   .slick-active {
        .rise-blocks-banner-has-bg {
            transform: scale(1, 1);
        }
   }
}